"use client";

import { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Paper,
  Divider,
  Button,
  Chip,
  Stack,
  useTheme,
  CircularProgress,
  Tabs,
  Tab,
  Card,
  CardContent,
  IconButton,
  Tooltip,
} from "@mui/material";
import {
  CheckCircle as CheckCircleIcon,
  Bookmark as BookmarkIcon,
  Share as ShareIcon,
  Code as CodeIcon,
  Lightbulb as LightbulbIcon,
  Timer as TimerIcon,
} from "@mui/icons-material";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { atomDark } from "react-syntax-highlighter/dist/cjs/styles/prism";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`problem-tabpanel-${index}`}
      aria-labelledby={`problem-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const ChapterDetail = ({ chapter, subtopics, progress, onToggleComplete }) => {
  const theme = useTheme();
  const [activeTab, setActiveTab] = useState(0);

  if (!chapter) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 4,
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          textAlign: "center",
          minHeight: 400,
          borderRadius: 2,
          backgroundColor: theme.palette.background.default,
          border: `1px solid ${theme.palette.divider}`,
        }}
      >
        <Typography variant="h6" color="text.secondary">
          Select a chapter to view details
        </Typography>
      </Paper>
    );
  }

  const currentSubtopic = subtopics.find((st) => st._id === activeTab);

  return (
    <Paper
      elevation={0}
      sx={{
        p: { xs: 2, md: 3 },
        height: "100%",
        borderRadius: 2,
        backgroundColor: theme.palette.background.default,
        border: `1px solid ${theme.palette.divider}`,
      }}
    >
      <Box sx={{ mb: 3 }}>
        <Typography variant="h5" component="h2" gutterBottom>
          {chapter.name}
        </Typography>
        <Typography variant="body1" color="text.secondary" paragraph>
          {chapter.description}
        </Typography>

        <Box sx={{ display: "flex", gap: 1, mb: 2 }}>
          <Chip
            label={chapter.difficulty}
            size="small"
            color={getLevelColor(chapter.difficulty)}
            variant="outlined"
          />
        </Box>

        <Divider sx={{ my: 2 }} />

        <Tabs
          value={activeTab}
          onChange={(e, newValue) => setActiveTab(newValue)}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ mb: 2 }}
        >
          {subtopics.map((subtopic) => (
            <Tab
              key={subtopic._id}
              value={subtopic._id}
              label={subtopic.name}
              icon={
                subtopic.isCompleted ? <CheckCircle fontSize="small" /> : null
              }
              iconPosition="end"
            />
          ))}
        </Tabs>

        {currentSubtopic && (
          <Box sx={{ mt: 2 }}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                mb: 2,
              }}
            >
              <Typography variant="h6">{currentSubtopic.name}</Typography>
              <Chip
                label={currentSubtopic.difficulty}
                size="small"
                color={getLevelColor(currentSubtopic.difficulty)}
              />
            </Box>

            <Box sx={{ mb: 3 }}>
              <Button
                variant={currentSubtopic.isCompleted ? "outlined" : "contained"}
                color="primary"
                startIcon={currentSubtopic.isCompleted ? <CheckCircle /> : null}
                onClick={() =>
                  onToggleComplete(chapter._id, currentSubtopic._id)
                }
                sx={{ mr: 2, mb: { xs: 2, sm: 0 } }}
              >
                {currentSubtopic.isCompleted
                  ? "Completed"
                  : "Mark as Completed"}
              </Button>
            </Box>

            {currentSubtopic.youtubeLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Video Tutorial
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.youtubeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<YouTube />}
                  sx={{ mr: 2, mb: 1 }}
                >
                  Watch on YouTube
                </Button>
              </Box>
            )}

            {currentSubtopic.leetcodeLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Practice Problems
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.leetcodeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<Code />}
                  sx={{ mr: 2, mb: 1 }}
                >
                  LeetCode Problems
                </Button>
              </Box>
            )}

            {currentSubtopic.articleLink && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" gutterBottom>
                  Read More
                </Typography>
                <Button
                  variant="outlined"
                  href={currentSubtopic.articleLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  startIcon={<Article />}
                >
                  Read Article
                </Button>
              </Box>
            )}
          </Box>
        )}
      </Box>
    </Paper>
  );
};
